.Task {
    width: 100%;
    background-color: #fff;
    box-shadow: 0 3px 7px 0 rgba(110, 142, 247, 0.13);
    border: 1px solid #E1E2E8;
    border-radius: 3px;
    padding: 15px 10px;
    box-sizing: border-box;
    margin-bottom: 15px;
}

.Task__editing {
    flex-grow: 2;
    word-wrap: break-word;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
}
.Task__editing-bgr {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(247, 248, 254, 0.5);
    width: 100vw;
    height: 100vh;
}
.Task__editor {
    width: 100%;
    margin-right: 5px;
    text-indent: 2px;
    font-size: 17px;
    box-sizing: border-box;
    z-index: 9;
}
.Task__editing-action {
    display: flex;
    z-index: 99;
}
.Task__editing-action i {
    cursor: pointer;
    color: #666666;
    margin-left: 5px;
}
.Task__editing-action i:first-child{
    color: #00DF82;
}
.Task__time {
    font-size: 13px;
    color: #666666;
    margin-bottom: 10px;
}
.Task__time i{
    margin-right: 5px;
}
.Task__main {
    display: flex;
}
.Task__content {
    flex-grow: 2;
    padding-right: 10px;
    word-wrap: break-word;
    font-size: 17px;
}
.Task__action {
    display: flex;
}
.Task__btn {
    margin: 0 5px;
    cursor: pointer;
}
.Task__btn:first-child{
    color: #5680f9;
}
.Task__btn:nth-child(2){
    color: #FF2F2F;
}

