.AddNewModal {
    position: fixed;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    border: 1px solid #5680f9;
}

.AddNewModal__backdrop {
position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(33, 33, 33, 0.3);
}
.AddNewModal__content {
    position: relative;
    z-index: 9;
    width: 350px;
    background-color: #fff;
    padding: 10px 15px;
    border-radius: 5px;
    box-sizing: border-box;
    margin-top: -10%;
}
.AddNewModal__title {
    font-weight: bold;
    letter-spacing: 0.5px;
    line-height: 30px;
    box-sizing: border-box;
    border-bottom: 1px solid #ccc;
    color: #5680f9;
}
.AddNewModal__task-status {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.AddNewModal__radio {
    display: flex;
    justify-content: center;
    align-items: center;
}
.AddNewModal__task {
    margin: 7px 0 25px;
    box-sizing: border-box;
}
.AddNewModal__input {
    line-height: 30px;
    width: 100%;
    border-radius: 3px;
    border: 1px solid #ccc;
    text-indent: 7px;
}
.AddNewModal__action {
    text-align: right;
}
.AddNewModal__btn {
    border: 1px solid transparent;
    width: 70px;
    box-sizing: border-box;
    line-height: 25px;
    margin: 0 7px;
    border-radius: 3px;
    font-size: 15px;
    cursor: pointer;
}
.AddNewModal__btn:last-child {
    margin-right: 0;
}
.AddNewModal__btn--confirm {
    background-color: #5680f9;
    color: #fff;
    border-color: #5680f9;
}
.AddNewModal__btn--cancel {
    background-color: #fff;
    border-color: #666A95;
    color: #666A95;
}